import React, {useState} from 'react';
import {db} from '../firebase';

const Firestore = (props) => {

  const [tareas, setTareas] = useState([])
  const [tarea,setTarea] = useState('')
  const [modoEdicion, setModoEdicion] = useState(false)
  const [id, setId] = useState('')

  React.useEffect(() => {
    const obtenerDatos = async () => {
      try {
        
        const data = await db.collection(props.user.uid).get()

        //...doc.data porque es otro objeto y así no se crean objetos dentro del array
        const arrayData = data.docs.map(doc => ({id : doc.id, ...doc.data() }))

        setTareas(arrayData)

      } catch (error) {
        console.log(error)
      }
    }

    obtenerDatos()
  }, [props.user.uid])

  const agregar = async (e) => {
    e.preventDefault()
    

    if (!tarea.trim()) {
      console.log('elemento vacío')
      return
    }

    try {
      
      const nuevaTarea ={
        name : tarea,
        fecha : Date.now()
      }

      //función que añade en db
      const data = await db.collection(props.user.uid).add(nuevaTarea)

      //actualizamos tareas para que salga en pantalla la nueva tarea
      setTareas([
        ...tareas,
        {...nuevaTarea, id : data.id}
      ])

      //vaciamos campo tarea
      setTarea('')

    } catch (error) {
      console.log(error)
    }
  }


  const eliminar = async (id) => {
    try {
    
      await db.collection(props.user.uid).doc(id).delete()


      const arrayFilter = tareas.filter(item => item.id !== id)
      setTareas(arrayFilter)


    } catch (error) {
      console.log(error)
    }
  }

  const activarEdicion = (item) => {
    setModoEdicion(true)
    setTarea(item.name)
    setId(item.id)
  }

  const editar = async (e) => {
    e.preventDefault()

    if (!tarea.trim()) {
      console.log('elemento vacío')
      return
    }

    try {
      
      await db.collection(props.user.uid).doc(id).update({
        name : tarea
      })

      const arrayEditado = tareas.map(item => (
        item.id === id ? {id : item.id, fecha : item.fecha, name : tarea} : item
      ))

      setTareas(arrayEditado)
      setModoEdicion(false)
      setTarea('')
      setId('')

    } catch (error) {
      console.log(error)
    }
  }

  return (
    <div className="container mt-3">
      <div className="row">
        <div className="col-md-6">
          <ul className="list-group">
            {
              tareas.map(item => (
                <li className="list-group-item" key={item.id}>
                  {item.name}
                  <button className="btn btn-danger btn-sm float-right ml-2" onClick={() => eliminar(item.id)}>Eliminar</button>
                  <button className="btn btn-warning btn-sm float-right" onClick={() => activarEdicion(item)}>Editar</button>
                </li>
              ))
            }
          </ul>
        </div>
        <div className="col-md-6">
          <h3>
            {
              modoEdicion ? 'Editar tarea' : 'Agregar tarea'
            }
          </h3>
          <form onSubmit={modoEdicion ? editar : agregar}>
            <input 
            type="text"
            placeholder={modoEdicion ? 'Edita la tarea' : 'Agrega una tarea'}
            className="form-control mb-2"
            onChange={e => setTarea(e.target.value)}
            value={tarea}
            />

            <button className={
              modoEdicion ? 'btn btn-warning btn-block' : 'btn btn-dark btn-block'
            } type="submit">
              {
                modoEdicion ? 'Editar' : 'Añadir'
              }
            </button>
          </form>
        </div>
      </div>
    </div>
  );
}

export default Firestore;
